.eltdf-main-menu {
    @include eltdfRelativeHolderLayout();
    height: 100%;
    width: auto;

    ul {
        list-style: none outside none;
        margin: 0;
        padding: 0;
        text-align: left;

        li {
            position: relative;
            display: inline-block;
            float: left;
            padding: 0;
            margin: 0;

            a {

                i:before {
                    margin: 0 7px 0 0;
                }

                .menu_icon_wrapper {
                    position: relative;
                    display: inline-block;
                    vertical-align: middle;
                }

                .eltdf-menu-featured-icon {
                    position: relative;
                    top: -7px;
                    left: 5px;
                    color: #a1a1a1;
                    font-size: 8px;
                    margin: 0;

                    &:before {
                        margin: 0;
                    }
                }
            }
        }
    }

    > ul {
        @include eltdfRelativeHolderLayout();
        height: 100%;

        > li {
            float: left;
            height: 100%;
            box-sizing: border-box;

            > a {
                @include eltdfRelativeHolderLayout();
                height: 100%;
                padding: 0 22px;
                color: $default-heading-color;
                font-size: 13px;
                font-weight: 600;
                line-height: 23px;
                letter-spacing: 1.5px;
                text-transform: uppercase;
                box-sizing: border-box;

                > span.item_outer {
                    position: relative;
                    display: block;
                    top: 50%;
                    @include eltdfTransform(translateY(-50%));

                    .item_text {
                        position: relative;
                        display: inline-block;
                        vertical-align: top;
                    }
                }

                &:hover {
                    color: #a7a9af;
                }
            }

            &.eltdf-active-item > a {
                color: #a7a9af;
            }

            &.has_sub {

                > a .eltdf-menu-arrow {
                    display: none;
                }
            }
        }
    }
}

.eltdf-light-header .eltdf-page-header > div:not(.eltdf-sticky-header):not(.fixed) {

    .eltdf-main-menu > ul > li {

        > a {
            color: $header-light-color;

            &:hover {
                color: $header-light-hover-color;
            }
        }

        &.eltdf-active-item > a {
            color: $header-light-hover-color;
        }
    }
}

.eltdf-dark-header .eltdf-page-header > div:not(.eltdf-sticky-header):not(.fixed) {

    .eltdf-main-menu > ul > li {

        > a {
            color: $header-dark-color;

            &:hover {
                color: $header-dark-color;
            }
        }

        &.eltdf-active-item > a {
            color: $header-dark-color;
        }
    }
}

/* Narrow drop down styles
   ========================================================================== */

.eltdf-drop-down,
.eltdf-vertical-menu {

    .second {
        position: absolute;
        display: block;
        left: 0;
        top: 100%;
        visibility: hidden;
        overflow: hidden;
        opacity: 0;
        z-index: 10;
        @include eltdfTransition(top 0.15s ease-out);

        &.eltdf-drop-down-start {
            visibility: visible;
            overflow: visible;
            opacity: 1;
            @include eltdfTransition(opacity 0.2s ease-out);
        }

        &.right {
            right: 0;
            left: auto;
        }
    }

    .second .inner {
        position: relative;
        display: block;
        padding: 0;
        margin: 0;
        z-index: 997;
        

        ul {

            li {
                display: block;
                float: none;
                height: auto;

                .widget {
                    width: 100%;
                    margin: 0 !important;
                }

                a {
                    display: block;
                    float: none;
                    margin: 0;
                    padding: 7px 30px;
                    color: $default-text-color;

                    .item_outer {
                        position: relative;
                        display: block;
                        overflow: hidden;
                        box-sizing: border-box;
                    }

                    &:hover {
                        color: #000;
                    }
                }

                &.current-menu-ancestor > a,
                &.current-menu-item > a {
                    color: #000;
                }

                &.sub {

                    > a {

                        .item_outer {

                            &:after {
                                position: absolute;
                                display: inline-block;
                                vertical-align: middle;
                                top: 0;
                                right: 0;
                                content: "\e87a";
                                font-family: 'Linearicons-Free';
                                font-size: 12px;
                                font-weight: normal;
                                font-style: normal;
                                text-rendering: auto;
                                -webkit-font-smoothing: antialiased;
                                -moz-osx-font-smoothing: grayscale;
                            }
                        }
                    }
                }
            }

            &.right {

                li {

                    &.sub {

                        > a {

                            .item_outer {

                                &:after {
                                    right: auto;
                                    left: 2px;
                                    content: "\e879";
                                }
                            }
                        }
                    }

                    a {
                        text-align: right;
                    }

                    ul {
                        left: -100%;
                    }
                }
            }
        }
    }

    li.left_position .second {
        left: 0;
    }

    li.right_position .second {
        left: auto;
        right: 0;
    }

    > ul > li {
        &:hover > .second {
            z-index: 20;
        }
    }

    .narrow .second .inner {

        ul {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            background-color: #fff;

            li {
                width: 220px;

                ul {
                    position: absolute;
                    left: 100%;
                    height: auto;
                    opacity: 0;
                    overflow: hidden;
                    z-index: 10;                    
                    -webkit-transform: translateZ(0);
                    @include eltdfTransition(opacity .4s ease-out);
                }

                &:hover ul {
                    z-index: 20;
                    opacity: 1;
                }
            }

            &.right li ul {
                left: -100%;
            }
        }
    }
}

.eltdf-drop-down {
    .second .inner,
    .narrow .second .inner ul li ul {
        box-shadow: 0 5px 5px 0 rgba(0,0,0,0.03);
    }

    .narrow .second .inner {
        ul {
            padding: 22px 0 25px;

            li {
                ul {
                    top: -22px;
                }
            }
        }
    }

    .second {
        left: -6px;

        &.right {
            right: -6px;
        }
    }
}

.eltdf-vertical-menu {
    .second {
        left: calc(100% + 68px);
        top: -19px;
    }
}

.eltdf-header-vertical-compact {
    .eltdf-vertical-menu {
        .second {
            left: 100%;
            top: 0;
        }
    }
}

/* Wide drop down styles
   ========================================================================== */
.eltdf-drop-down .wide {

    .second {

        .inner {
            background-color: #fff;
            text-align: center;

            ul {

                li {

                    a {
                        padding: 7px 0;
                    }

                    ul {
                        position: relative;
                        display: block;
                        top: 0;
                        left: 0;
                        visibility: visible;

                        li {

                            &:last-child {

                                a {
                                    padding-bottom: 0;
                                }
                            }
                        }
                    }
                }
            }

            > ul {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                width: 1100px;
                box-sizing: border-box;
                padding: 35px 0 30px;

                @media only screen and (min-width: 1400px) {
                    .eltdf-grid-1300 & {
                        width: $grid-width-1300;
                    }
                }

                @media only screen and (min-width: 1300px) {
                    .eltdf-grid-1200 & {
                        width: $grid-width-1200;
                    }
                }

                @media only screen and (min-width: 1200px) {
                    .eltdf-grid-1000 & {
                        width: $grid-width-1000;
                    }
                }

                @media only screen and (min-width: 1024px) {
                    .eltdf-grid-800 & {
                        width: $grid-width-800;
                    }
                }

                > li {
                    float: left;
                    width: 25%;
                    margin: 0;
                    box-sizing: border-box;

                    &:last-child {
                        .widget_text {
                            padding-top: 8px;
                        }
                    }

                    &:nth-child(4n+1) {
                        clear: both;
                    }

                    > a {
                        padding: 0 0 13px;
                        color: $default-heading-color;
                        font-size: 13px;
                        line-height: 1.1em;
                        font-weight: 600;
                        letter-spacing: 1.5px;
                        text-transform: uppercase;

                        &.no_link {
                            cursor: default;
                        }

                        .item_outer {
                            padding: 0;
                            overflow: inherit;

                            &:before,
                            &:after {
                                display: none;
                            }
                        }
                    }

                    &.current-menu-ancestor > a,
                    &.current-menu-item > a {
                        color: $default-heading-color;
                        font-weight: 600;
                        letter-spacing: 1.5px;
                    }
                }
            }
        }
    }

    &.left_position,
    &.right_position {

        .second .inner > ul {
            width: auto;
            white-space: nowrap;

            > li {
                float: none;
                display: inline-block;
                vertical-align: top;

                &:nth-child(4n+1) {
                    border-left: none;
                }
            }
        }

        .second ul li {
            width: 200px;
        }
    }
}

.eltdf-full-width-wide-menu {
    .eltdf-drop-down .wide .second {
        background-color: #fff;

        .inner {
            background-color: transparent !important;
        }
    }

    .wide .second .inner {
        text-align: center;
    }

    .second .inner ul {
        text-align: left;
    }
}