/* ==========================================================================
   Blog List shortcode Standard - begin
   ========================================================================== */

.eltdf-blog-list-holder {
    &.eltdf-bl-standard {
        &:not(.eltdf-bl-one-column) {

            .eltdf-bl-item {
                float: left;
            }
        }

        $columns_number: ('two', 'three', 'four', 'five');

        @for $i from 0 to length($columns_number) {
            &.eltdf-bl-#{nth($columns_number,$i+1)}-columns {

                .eltdf-bl-item {
                    width: 100% / ($i+2);
                }

                @if ($i > 1) { // set different break point for four and five columns
                    @media only screen and (min-width: $laptop-landscape-plus-pixel) {

                        .eltdf-bl-item {

                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    }
                } @else {
                    @media only screen and (min-width: $ipad-landscape-plus-pixel) {

                        .eltdf-bl-item {

                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    }
                }
            }
        }

        .eltdf-post-title {
            margin: 0 0 -30px;
        }

        .eltdf-bli-excerpt {
            margin-top: -5px;

            .eltdf-post-read-more-button {
                a {
                    margin-top: 9px;
                    i {
                        color: $first-main-color;
                        font-size: 13px;
                    }
                }
            }
        }

        .eltdf-post-info-date {
            margin-top: 23px;
            margin-bottom: -10px;
            padding-left: 3px;
            a {
                font-size: 11px;
                letter-spacing: .2em;
                line-height: 40px;
                text-transform: uppercase;
                font-weight: 400;
                color: $default-text-color;
            }
        }

        .eltdf-bli-info {
            @include eltdfRelativeHolderLayout();
            padding-top: 22px;
            box-sizing: border-box;
            clear: both;

            .eltdf-post-info-author {
                text-transform: uppercase;
                display: inline-block;
                font-size: 11px;
                font-weight: 400;
                letter-spacing: 2px;
                margin-right: 11px;
                .eltdf-post-info-author-link {
                    font-weight: 500;
                }
            }

            .eltdf-post-info-comments-holder {
                display: inline-block;
                margin-right: 11px;

                a {
                    font-size: 11px;
                    color: $default-heading-color;
                    font-weight: 500;
                    &:before {
                        content: "\76";
                        font-family: 'ElegantIcons';
                        font-size: 14px;
                        padding-right: 5px;
                        color:#cacaca;
                        position: relative;
                        top: 2px;
                    }
                }
            }

            .eltdf-post-info-category {
                display: inline-block;
                a {
                    text-transform: uppercase;
                    font-size: 11px;
                    letter-spacing: 2px;
                    color: $default-heading-color;
                    font-weight: 500;
                }

                 &:before {
                    content: "\e012";
                    font-family: 'ElegantIcons';
                    font-size: 14px;
                    padding-right: 5px;
                    color:#cacaca;
                    position: relative;
                    top: 2px;
                }
            }
        }
    }
}

@media only screen and (max-width: 1024px) {
    .eltdf-blog-list-holder.eltdf-bl-standard.eltdf-bl-three-columns .eltdf-bl-item, {
        width: 33.333333% !important;
        float: none;
    }
}

@media only screen and (max-width: 768px) {
    .eltdf-blog-list-holder.eltdf-bl-standard.eltdf-bl-three-columns .eltdf-bl-item,
     .eltdf-blog-list-holder.eltdf-bl-standard.eltdf-bl-four-columns .eltdf-bl-item,
     .eltdf-blog-list-holder.eltdf-bl-standard.eltdf-bl-five-columns .eltdf-bl-item {
        width: 100% !important;
    }
}

/* ==========================================================================
   Blog List shortcode Standard - end
   ========================================================================== */