.eltdf-header-vertical-closed {
    .menu-item .second {
        @include eltdfTransition(opacity .1s);
    }
    .menu-item .second.eltdf-float-open {
        margin-left: 0px;
        opacity: 1;
        visibility: visible;
        overflow: visible;
        @include eltdfTransition(opacity .25s ease-out);
    }
}