.eltdf-blog-list-holder {
    &.eltdf-bl-masonry {

        transition: none !important;
        overflow: visible !important;

        .eltdf-blog-list {
            opacity: 0;
            @include eltdfTransition(opacity .2s ease-out);
        }

        .eltdf-bl-item {
            vertical-align: top;
            z-index: 100;
        }

        .eltdf-bl-item,
        .eltdf-bl-grid-sizer {
            width: 100%;
        }

        .eltdf-bl-grid-gutter {
            width: 0;
        }

        $masonry_columns_number: ('two', 'three', 'four', 'five');

        @for $i from 0 to length($masonry_columns_number) {
            &.eltdf-bl-#{nth($masonry_columns_number,$i+1)}-columns {
                $column_width: 100%/($i+2);

                .eltdf-bl-item,
                .eltdf-bl-grid-sizer {
                    width: $column_width;
                }
            }
        }

        .eltdf-post-excerpt-holder {
            @include eltdfRelativeHolderLayout();
            margin: 0 0 20px;
            margin-top: -19px;
        }

        .eltdf-post-title {
            margin: -10px 0 0;
        }

        .eltdf-bli-excerpt {
            margin-top: -37px;
            .eltdf-post-excerpt-holder {
                p {
                    padding-top: 53px;
                }
            }

            .eltdf-post-read-more-button {
                a {
                    margin-top: -12px;
                    i {
                        color: $first-main-color;
                        font-size: 13px;
                    }
                }
            }
        }

        .eltdf-post-info-date {
            margin-top: 25px;
            a {
                font-size: 11px;
                letter-spacing: .2em;
                line-height: 40px;
                font-weight: 500;
                color: $default-text-color;
            }
        }

        .eltdf-bli-info {
            @include eltdfRelativeHolderLayout();
            margin: 5px 0;
            clear: both;

            .eltdf-post-info-author {
                text-transform: uppercase;
                display: inline-block;
                font-size: 11px;
                font-weight: 400;
                letter-spacing: 2px;
                margin-right: 11px;
                .eltdf-post-info-author-link {
                    font-weight: 500;
                }
            }

            .eltdf-post-info-comments-holder {
                display: inline-block;
                margin-right: 11px;

                a {
                    font-size: 11px;
                    color: $default-heading-color;
                    font-weight: 500;
                    &:before {
                        content: "\76";
                        font-family: 'ElegantIcons';
                        font-size: 14px;
                        padding-right: 5px;
                        color:#cacaca;
                        position: relative;
                        top: 2px;
                    }
                }
            }

            .eltdf-post-info-category {
                display: inline-block;
                a {
                    text-transform: uppercase;
                    font-size: 11px;
                    letter-spacing: 2px;
                    color: $default-heading-color;
                    font-weight: 500;
                }

                 &:before {
                    content: "\e012";
                    font-family: 'ElegantIcons';
                    font-size: 14px;
                    padding-right: 5px;
                    color:#cacaca;
                    position: relative;
                    top: 2px;
                }
            }
        }
    }
}