.eltdf-header-top-menu {
    .eltdf-page-header {
        .eltdf-logo-area {
            height: 80px;
            overflow: visible;
	        margin-top: 70px;
	        z-index: 99;

            .eltdf-logo-wrapper a {
                max-height: 72px; //80*0.9
            }

            box-sizing: border-box;
        }

        .eltdf-menu-area {
            position: fixed;
	        left: 0;
	        top: 0;
	        width: 100%;
	        height: 70px;
	        z-index: 100;

            box-sizing: border-box;

            .eltdf-vertical-align-containers {
                .eltdf-position-left-inner {
                    height: 100%;
                }
            }

        }

        .eltdf-page-header .eltdf-sticky-header {
            .eltdf-position-left-inner {
                height: auto;
            }

            .eltdf-position-right-inner {
                height: 100%;
            }
        }
    }
}
.admin-bar.eltdf-header-top-menu {
	.eltdf-page-header {

		.eltdf-menu-area {
			top: 32px;
		}
	}
}
