/* ==========================================================================
   Blog List - Standard style - begin
   ========================================================================== */

.eltdf-blog-holder {
	
    &.eltdf-blog-standard {
	    
        article {
            margin: 0 0 67px;
            padding: 0;

            &.sticky {
                .eltdf-post-title {
                    a {
                        color: red;
                    }
                }
            }

            &.eltdf-post-has-media {
                .eltdf-post-heading {
                    margin-bottom: 25px;
                }
            }

            .eltdf-post-title {
                margin: -5px 0 18px;
            }

            .eltdf-post-excerpt-holder {
                @include eltdfRelativeHolderLayout();
                margin: -10px 0 24px;
                .eltdf-post-excerpt {
                  &:after {
                    content: none;
                  }
                }
            }

            .eltdf-more-link-container {
                @include eltdfRelativeHolderLayout();
                margin: 20px 0 0;
            }

            .eltdf-post-info-top {
                margin-bottom: 5px;

              .eltdf-post-info-date {
                a {
                  font-size: 11px;
                  letter-spacing: 0.2em;
                  line-height: 40px;
                  font-weight: 500;
                  color: $default-text-color;
                }
              }
	
	            > div {
		            position: relative;
		            display: inline-block;
		            padding-right: 25px;
		            margin-left: 5px;
		
		            &:after {
			            content: "/";
			            position: absolute;
			            top: 0;
			            right: 5px;
		            }
		
		            &:first-child {
			            margin-left: 0;
		            }
		
		            &:last-child {
			
			            &:after {
				            content: '';
			            }
		            }
		
		            span {
			            color: #444;
		            }
	            }
            }

            .eltdf-post-info-bottom {
                margin-top: -13px;

                .eltdf-post-info-bottom-left,
                .eltdf-post-info-bottom-right {
	                position: relative;
	                display: inline-block;
	                float: left;
                    line-height: 27px;
                }

                .eltdf-post-info-bottom-left {
                    width: 80%;

                }

                .eltdf-post-info-bottom-right {
                    width: 20%;

                }

                .eltdf-post-info-bottom-right {
	                
                    > div {
                        margin-left: 17px;
	                    
	                    &:first-child {
		                    margin-left: 0;
	                    }
                    }
                }

                .eltdf-post-info-author {
                    text-transform: uppercase;
                    display: inline-block;
                    font-size: 11px;
                    font-weight: 400;
                    letter-spacing: 2px;
                    margin-right: 11px;
                    .eltdf-post-info-author-link {
                        font-weight: 500;
                    }
                }

                .eltdf-post-info-bottom-right {
                    text-align: right;
                }

                .eltdf-post-info-comments-holder,
                .eltdf-post-info-category {
                    display: inline-block;
                    margin-right: 11px;

                    a {
                        display: inline-block;
                    }
                }

                .eltdf-post-info-comments-holder {
                    a {
                        font-size: 11px;
                        color: $default-heading-color;
                        font-weight: 500;
                        &:before {
                            content: "\76";
                            font-family: 'ElegantIcons';
                            font-size: 14px;
                            padding-right: 5px;
                            color:#cacaca;
                            position: relative;
                            top: 2px;
                        }
                    }
                }

                .eltdf-post-info-category {
                    a {
                        text-transform: uppercase;
                        font-size: 11px;
                        letter-spacing: 2px;
                        color: $default-heading-color;
                        font-weight: 500;
                    }

                     &:before {
                        content: "\e012";
                        font-family: 'ElegantIcons';
                        font-size: 14px;
                        padding-right: 5px;
                        color:#cacaca;
                        position: relative;
                        top: 2px;
                    }
                }

            }

            &.format-audio {
                &.eltdf-post-no-media.has-post-thumbnail {
                    .eltdf-post-heading {
                        margin-bottom: 30px;
                    }
                }
            }

            &.format-link {

                margin-bottom:80px;

                .eltdf-post-additional-content {
                    margin-bottom: 50px;
                }

                .eltdf-post-info-top {
                    margin-bottom: 21px;
                }

                .eltdf-post-text-main {
                    margin-bottom: 0;
                }

                .eltdf-post-text-inner {
                    margin-bottom: 0;
                }

                .eltdf-post-text {
                    padding: 30px 53px 37px;
                    background-color: #fbfbfb;
                }

                .eltdf-post-text-main {
                    @include eltdfTableLayout();
                }

                .eltdf-post-info-bottom {
                    text-align: center;
                    margin-top: 0;
                    margin-bottom: 0;
                }

                .eltdf-post-mark {
                    position: absolute;
                    top: -20px;
                    right: -68px;
                    width: 50px;

                    .eltdf-link-mark {
                        font-size: 24px;
                        color: #c7c7c7;
                    }
                }

                .eltdf-post-link-holder {
                    @include eltdfTableCellLayout();
                    text-align: center;
                }

                .eltdf-post-title {
                    margin: 15px 0 7px;

                    &:hover {
                        opacity: 1;
                    }
                }
            }

            &.format-quote {
	             
                 margin-bottom:80px;

                .eltdf-post-text {
                    padding: 30px 53px 45px;
	                background-color: #fafafa;
                }
	            
                .eltdf-post-text-main {
                    @include eltdfTableLayout();
                    text-align: center;
                }

                .eltdf-post-info-bottom {
                    .eltdf-post-info-bottom-left {
                        width: 100%;
                        text-align: center;
                    }
                }

                .eltdf-post-mark {
                    @include eltdfTableCellLayout();
                    width: 50px;
                    vertical-align: top;
                    text-align: left;

                    .eltdf-quote-mark {
                        padding-top: 5px;
                        font-size: 36px;
                        color: $first-main-color;
                    }
                }
	            
                .eltdf-post-link-holder {
                    @include eltdfTableCellLayout();
                    vertical-align: top;
                    text-align: left;
                }
	            
                .eltdf-post-title {
                    margin: 23px 0 5px;

                    &:hover {
                        opacity: 1;
                    }
                }

                .eltdf-quote-author {
                    font-size: 11px;
                    color: $default-heading-color;
                    letter-spacing: 2px;
                    font-weight: 500;
                }
	            
                .eltdf-post-author {
	                display: inline-block;
	                margin-bottom: 23px;
	                font-size: 13px;
	                line-height: 20px;
                }
            }

            &.format-gallery {
                .owl-nav {
                    .eltdf-prev-icon,
                    .eltdf-next-icon {
                            font-size: 30px;
                            color: #000;
                    }
                }
            }
        }
    }
}
/* ==========================================================================
   Blog List - Standard style - end
   ========================================================================== */