/* ==========================================================================
   Blog List widget style - begin
   ========================================================================== */

.widget.eltdf-blog-list-widget {
    margin: 0 0 20px;

    > h4 {
        margin: 0 0 15px;
    }

    footer .eltdf-footer-top &,
    .eltdf-top-bar & {

        > h2 {
            margin: 0 0 8px;
        }
    }
}

@include ipad-portrait {
  footer {
    .widget.eltdf-blog-list-widget {
      margin-top: 35px;
    }
  }
}
/* ==========================================================================
   Blog List widget style - end
   ========================================================================== */