/* ==========================================================================
# Side area style - start
========================================================================== */

/* Side area general style - start
========================================================================== */

.eltdf-light-header .eltdf-page-header > div:not(.eltdf-sticky-header):not(.fixed) .eltdf-side-menu-button-opener,
.eltdf-light-header .eltdf-top-bar .eltdf-side-menu-button-opener {
	color: $header-light-color !important; //Color can be set inline from widget opener;
	
	&.opened,
	&:hover {
		color: $default-text-color !important; //Color can be set inline from widget opener;
	}
}

.eltdf-dark-header .eltdf-page-header > div:not(.eltdf-sticky-header):not(.fixed) .eltdf-side-menu-button-opener,
.eltdf-dark-header .eltdf-top-bar .eltdf-side-menu-button-opener {
	color: $header-dark-color !important; //Color can be set inline from widget opener;
	
	&.opened,
	&:hover {
		color: $default-text-color !important; //Color can be set inline from widget opener;
	}
}

.eltdf-side-menu-button-opener {
	position: relative;
	display: inline-block;
	vertical-align: middle;
	margin: 0 14px;
	
	.eltdf-side-menu-title {
		position: relative;
		display: inline-block;
		vertical-align: middle;
		margin: 0 8px 0 0;
		color: currentColor;
	}
	
	.eltdf-side-menu-lines {
		position: relative;
		display: inline-block;
		vertical-align: middle;
		width: 23px;
		height: 14px;
		top: -1px;
		
		.eltdf-side-menu-line {
			position: absolute;
			left: 0;
			height: 1px;
			width: 100%;
			background-color: currentColor;
			border-radius: 12px;
			@include eltdfTransition(background-color .2s ease-out);
			
			&.eltdf-line-1 {
				top: 0;
				outline: transparent solid 1px;
			}
			
			&.eltdf-line-2 {
				top: 50%;
				outline: transparent solid 1px;
			}
			
			&.eltdf-line-3 {
				top: 100%;
				outline: transparent solid 1px;
			}
		}
	}
	
	&.opened,
	&:hover {
		color: $default-text-color;
	}
}

.eltdf-side-menu {
    position: fixed;
    top: 0;
	right: -430px;
	width: 430px;
    height: 100%;
    min-height: 100%;
	padding: 40px 60px 30px;
    background-color: #fff;
    overflow: hidden;
    visibility: hidden;
    z-index: 9999;
    -webkit-backface-visibility: hidden;
    box-sizing: border-box;
	box-shadow: -3px 0 3px rgba(0, 0, 0, 0.04);
    @include eltdfTransition(all .6s cubic-bezier(.77,0,.175,1));
	text-align: left;

	.widget_nav_menu {

		ul {
			li {

				line-height: 27px;
			}
		}
	}

	.eltdf-side-area-inner {
		height: 100%;
		min-height: 100%;
		overflow: hidden;
		outline: none;
	}

	div.widget_eltdf_instagram_widget {
		padding: 0 50px;
		margin-top: -8px;
	}

	div.widget_text {
		.eltdf-widget-title-holder {
			.eltdf-widget-title {
				color: #fff;
				letter-spacing: 0.1em;
				margin-top: 33px;
			}
		}

		.textwidget {
			font-size: 24px;
			color: $default-text-color;
			font-weight: 300;
			line-height: 36px;

			p {
				font-size: 12px;
				color: #a5a5a5;
				letter-spacing: 0.1em;
				font-weight: bold;
			}
		}
	}

	.eltdf-side-area-bottom {
		.widget {
			margin-bottom: -3px;
			margin-top: -15px;
		}

		.eltdf-social-icon-widget-holder {
			font-size: 15px;
			color:	#fff;
		}
	}

    .eltdf-close-side-menu-holder {
        position: absolute;
	    top: 38px;
	    right: 38px;
        z-index: 1000;
    }

	a.eltdf-close-side-menu {
		display: block;
		color: #000;
		font-size: 37px;
		z-index: 1000;

		&:hover {
			color: $default-text-color;
		}

		span {
			display: block;
			line-height: inherit;

			&:before {
				display: block;
				line-height: inherit;
			}
		}
	}
}

.admin-bar .eltdf-side-menu .eltdf-close-side-menu-holder {
    top: 32px;
}

.eltdf-top-bar-enabled .eltdf-side-menu .eltdf-close-side-menu-holder {
    top: 74px;
}

/* Side area general style - end
========================================================================== */

/* Side area slides over content style - start
========================================================================== */

.eltdf-side-menu-slide-from-right {

    .eltdf-wrapper {

        .eltdf-cover {
            z-index: -1;
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            overflow: hidden;
            opacity: 0;
        }
    }
}

.eltdf-side-menu-slide-from-right.eltdf-right-side-menu-opened {

    .eltdf-wrapper {
        .eltdf-cover {
            z-index: 3000;
        }
    }

    .eltdf-side-menu {
        right: 0;
        visibility: visible;
    }
}

.eltdf-side-menu .widget .eltdf-widget-title-holder .eltdf-widget-title {
	margin-bottom: 1px !important;
}

/* Side area slides over content style - end
========================================================================== */

/* ==========================================================================
# Side area style - end
========================================================================== */