/* ==========================================================================
   Footer style - begin
   ========================================================================== */
/* ==========================================================================
   #Content bottom styles - start
   ========================================================================== */
.eltdf-content-bottom {

    .eltdf-content-bottom-inner{
        box-sizing: border-box;
        
        &.eltdf-grid{
            padding: 20px 0;
        }
        
        &.eltdf-full-width{
            padding: 20px 50px;
        }
    }
}

.eltdf-page-footer {
    position: relative;
    display: block;
    width: 100%;
    vertical-align: middle;
    z-index: 99; //smaller than content, since filter dropdown from products list can get over footer
    box-sizing: border-box;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    background-color: $first-main-color;

    // footer widgets style

    .eltdf-icon-widget-holder {
        .eltdf-icon-text {
            margin-left: 15px;
            font-size: 13px;
        }
    }

    .eltdf-blog-list-widget {
        .eltdf-blog-list-holder {
            .eltdf-post-title {
                line-height: 1em;
                text-transform: capitalize;
                margin-bottom: 7px;

                &:hover {
                    opacity:1;
                }

                a {
                    font-size: 13px;
                    font-weight: 300;
                    font-family: $default-text-font;
                    letter-spacing: 0;

                    &:hover {
                        color: rgb(167, 169, 175);
                    }
                }
            }

            .eltdf-post-info-date {
                line-height: 1em;
                padding-bottom: 10px;
                border-bottom: 1px solid #868686; 
            }

            .eltdf-bl-item {
                margin: 0 0 15px;

                &:last-child {
                    .eltdf-post-info-date {
                        border-bottom: none;
                    }
                }
            }
        } 
    }

    .textwidget {
        p {
            font-size: 13px;
        }
    }

    // end footer widgets style

    .textwidget {
        p {
            font-size: 13px;
        }
    }

    /* Light skin style */
    .eltdf-light {
        .widget {
            color: #ffffff;
            ul {
                li {
                    a {
                        color: #ffffff;
                    }
                }
            }

            &.widget_nav_menu {
                ul {
                    li {
                        a {
                            color: #ffffff;
                        }
                    }
                }
            }

            div.eltdf-widget-title-holder {
                .eltdf-widget-title {
                    color: #ffffff;
                }
            }


            ul li .eltdf-post-info-date  a{
                color: #868686;
            }
        }

    }

    .eltdf-footer-bottom-holder {

        .eltdf-footer-bottom-inner {
            box-sizing: border-box;

            .eltdf-light {

                .widget {
                    color: #ffffff;
                    &.widget_nav_menu {
                        ul li {
                            a {
                                color: #ffffff;
                            }
                        }
                    }
                }
            }
        }
    }

    /* Light skin style end */

    /* Dark skin style */
    .eltdf-dark {
        .widget {
            color: #000;
            ul {
                li {
                    a {
                        color: #000;
                        &:hover {
                            color: $default-text-color;
                        }
                    }
                }
            }

            &.widget_nav_menu {
                ul {
                    li {
                        a {
                            color: #000;
                            &:hover {
                                color: $default-text-color;
                            }
                        }
                    }
                }
            }

            div.eltdf-widget-title-holder {
                .eltdf-widget-title {
                    color: #000;
                }
            }


           ul li .eltdf-post-info-date a{
                color: #868686;
            }
        }

    }

    .eltdf-footer-bottom-holder {

        .eltdf-footer-bottom-inner {
            box-sizing: border-box;

            .eltdf-dark {

                .widget {
                    color: #000;
                    &.widget_nav_menu {
                        ul li {
                            a {
                                color: #000;
                            }
                        }
                    }
                }
            }
        }
    }

    /* Dark skin style end */

    .widget {
        color: $default-text-color;
        ul {
            li {
                padding-top: 4px;
                a {
                    color: $default-text-color;
                    font-size: 13px;
                }
            }
        }

        &.widget_nav_menu {
            border-top: 0;
            ul {
                li {
                    padding-top: 4px;
                    a {
                        color: $default-text-color;
                        font-size: 13px;
                    }
                }
            }
        }

        div.eltdf-widget-title-holder {
            .eltdf-widget-title {
                color: $default-heading-color;
                letter-spacing: 0.1em;
                margin-top: -3px;
            }
        }
    }

    .eltdf-footer-top-holder {
        @include eltdfRelativeHolderLayout();
        box-sizing: border-box;

        .eltdf-footer-top-alignment-center{
            text-align: center;
        }
	    
        .eltdf-footer-top-alignment-left{
            text-align: left;
        }
	    
        .eltdf-footer-top-alignment-right{
            text-align: right;
        }

        .eltdf-footer-top-inner{
            box-sizing: border-box;
	        
            &.eltdf-grid{
                padding: 85px 0 25px;
            }
	        
            &.eltdf-full-width{
                padding: 76px 50px;
            }
        }
    }

    .eltdf-footer-bottom-holder {
        @include eltdfRelativeHolderLayout();
        box-sizing: border-box;
        text-align: center;

        .eltdf-footer-bottom-column-1 {
            text-align: left;
        }

        .eltdf-footer-bottom-column-3 {
            text-align: right;
        }

        .eltdf-footer-bottom-inner {

            box-sizing: border-box;

            .eltdf-grid-col-6:first-child {
                text-align: left;
            }

            .eltdf-grid-col-6:last-child {
                text-align: right;
            }

            .eltdf-grid-col-4:first-child {
                text-align: left;
            }

            .eltdf-grid-col-4:last-child {
                text-align: right;
            }

            .widget {
                margin: 0;
                font-size: 12px;
                padding-top: 0px;
                display: inline-block;
                vertical-align: middle;

                &.widget_nav_menu {
                    text-align: left;

                    ul {
                        padding: 0;

                        li {
                            padding: 0;
                            float: left;

                            a {
                                color: $default-text-color;
                                font-size: 12px;
                                padding: 0 10px;
                                transition: none;

                                &:after {
                                    content: '';
                                }

                                &:first-child {
                                    padding-left: 0;
                                }

                                &:hover {
                                    padding: 0 10px;
                                    &:first-child {
                                        padding-left: 0;
                                    }
                                }
                            }
                        }
                    }
                }
            }
	        
            &.eltdf-grid {
                .eltdf-grid-row {
                    padding: 30px 0;
                }
            }
	        
            &.eltdf-full-width {
                padding: 19px 50px;
            }
        }
    }
}

.eltdf-footer-bottom-holder {
    .show-footer-bottom-border {
       border-top: 1px solid #868686;

    }
}


/* ==========================================================================
   Footer style - end
   ========================================================================== */