.eltdf-header-minimal {
    .eltdf-page-header .eltdf-menu-area {
        box-sizing: border-box;

        .eltdf-position-right-inner {
            height: auto;
        }
    }

    .eltdf-position-left:before,
    .eltdf-position-right:before {
        content: "";
    }

    .eltdf-page-header .eltdf-sticky-header .eltdf-position-left-inner {
        height: auto;
    }

    .eltdf-sticky-header .eltdf-vertical-align-containers .eltdf-position-right .eltdf-position-right-inner {
        height: auto;
    }

}

