.eltdf-header-standard {
	.eltdf-menu-area {
		box-sizing: border-box;

        .eltdf-position-center-inner {
            height: 100%;
        }

        .eltdf-position-right-inner {
            height: 100%;

            .eltdf-main-menu-widget-area {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                height: 100%;

                .eltdf-main-menu-widget-area-inner {
                    height: 100%;
                }
            }
        }
	}

