$top-bar-height: 36px;

.eltdf-top-bar-background {
	position: absolute;
	height: 100px;
	width: 100%;
	left: 0;
	top: 0;
	background-color: transparent;
	z-index: 101;
}

.eltdf-header-box .eltdf-top-bar {
	background-color: transparent;
}

.eltdf-top-bar {
	width: 100%;
	background-color: transparent;
	color: #000;
	position: relative;
	font-weight: 400;
	height: $top-bar-height;
	font-size: 12px;
	z-index: 111;
	box-sizing: border-box;
	display: inline-block;
	vertical-align: middle;

	.eltdf-grid {
		height: 100%;

		.eltdf-vertical-align-containers {
			padding-left: 0;
			padding-right: 0;

			.eltdf-top-bar-in-grid-padding & {
				padding: 0px 25px;
			}
		}
	}

	.eltdf-vertical-align-containers {
		.eltdf-position-left:before,
		.eltdf-position-center:before,
		.eltdf-position-right:before {
			margin-right: 0px;
		}

	}

	.eltdf-top-bar-widget-area,
	.eltdf-top-bar-widget-area-inner {
		height: 100%;
	}

	.eltdf-top-bar-widget {
		display: table;
		float: left;
		height: 100%;
        margin-bottom: 0;

		padding-right: 24px;

		&:last-child {
			padding-right: 0;
		}

		&.widget_text {
			position: relative;
		}

		&.widget_eltdf_search_opener {
			.eltdf-top-bar-widget-inner {
				padding-top: 5px;
			}
		}

		&.widget_nav_menu {
			padding-top: 0;
			border-top: none;

			ul li {
				display: inline-block;
                padding: 0 22px;
                vertical-align: middle;

                a {
                    

                    &:after {
                        display: none;
                    }
                    &:hover {
                        padding-left: 0;
                    }
                }
			}
		}
	}

	.textwidget {
		p {
			margin: 5px 0;
		}
	}

	.eltdf-top-bar-widget-inner {
		display: table-cell;
		vertical-align: middle;
	}

	.eltdf-logo-wrapper a {
		max-height: $top-bar-height;
	}

}

.eltdf-top-bar-light .eltdf-top-bar {
	color: $header-light-color; //Beacause of inline color styles

	.widget:not(.widget_eltdf_woocommerce_dropdown_cart) {
		color: $header-light-color; //Beacause of inline color styles

		p,
		a,
		span,
		i,
		.widgettitle {
			color: $header-light-color !important; //Beacause of inline color styles
		}

	}

	.eltdf-shopping-cart-holder .eltdf-header-cart {
		.eltdf-cart-icon {
			color: #fff;
		}

		.eltdf-cart-number {
			background-color: #fff;
			color: #9e9e9e;
		}
	}
}

.eltdf-top-bar-dark .eltdf-top-bar {
	color: $header-dark-color; //Beacause of inline color styles

	.widget {
		color: $header-dark-color; //Beacause of inline color styles

		p,
		a,
		span,
		i,
		.widgettitle {
			color: $header-dark-color !important; //Beacause of inline color styles
		}

	}
}

.eltdf-top-bar-gray .eltdf-top-bar {
	color: #777777; //Beacause of inline color styles

	.widget {
		color: #777777; //Beacause of inline color styles

		p,
		a,
		span,
		i,
		.widgettitle {
			color: #777777; //Beacause of inline color styles
		}

	}
}

.eltdf-top-bar-light .eltdf-top-bar .widget:not(.widget_eltdf_woocommerce_dropdown_cart) {
	a:hover {
	   color: rgb(167, 169, 175) !important;
    }
} 