/* ==========================================================================
   Blog Single Standard style - begin
   ========================================================================== */

.eltdf-blog-holder.eltdf-blog-single {

    .eltdf-post-title:hover {
       opacity: 1;
    }

    .eltdf-post-title {
        margin: 13px 0;
    }

    &.eltdf-blog-single-standard {
        width: 66%;
        margin-left: auto;
        margin-right: auto;
        display: block;
    
        article {
            margin: 0;

            .eltdf-post-text-inner {
                margin-bottom: 68px;
            }

            .eltdf-post-text-main {
                margin-bottom: 61px;
            }

            .eltdf-post-content {
                .eltdf-post-info-bottom {
                    text-align: center;
                    margin-top: 0;
                    margin-bottom: 0;
                }
            }

            .eltdf-tags {
                a {
                    display: inline-block;
                    text-transform: uppercase;
                    color: $default-text-color;
                    font-size:11px;
                    font-weight: 400;
                    padding: 0 25px;
                    background-color: transparent;
                    border: 1px solid #cecece;
                    height: 29px;
                    line-height: 29px;
                    margin-right: 9px;
                    margin-bottom: 10px;
                    letter-spacing: 2px;
                }
            }
        
            .eltdf-post-heading {
                margin-bottom: 32px;
            }
        
            .eltdf-post-title {
                &:hover {
                    opacity:1;
                }
            }

            .eltdf-post-info-date {
                a {
                    letter-spacing: 2px;
                    font-weight: 400;
                    font-size: 11px;
                }
            }
        
            .eltdf-post-text {
                padding: 0;
            }
        
            .eltdf-post-info-top {
                margin-bottom: 26px;

                .eltdf-post-info-author {
                    text-transform: uppercase;
                    font-size: 11px;
                    font-weight: 400;
                    letter-spacing: 2px;
                    .eltdf-post-info-author-link {
                        color: $default-heading-color;
                        font-weight: 500;
                    }
                }

                .eltdf-post-info-comments-holder {
                    a {
                        font-size: 11px;
                        color: $default-heading-color;
                        font-weight: 500;
                        &:before {
                            content: "\76";
                            font-family: 'ElegantIcons';
                            font-size: 14px;
                            padding-right: 5px;
                            color:#cacaca;
                            position: relative;
                            top: 2px;
                        }
                    }
                }

                .eltdf-post-info-category {
                    a {
                        text-transform: uppercase;
                        font-size: 11px;
                        letter-spacing: 2px;
                        color: $default-heading-color;
                        font-weight: 500;
                    }

                     &:before {
                        content: "\e012";
                        font-family: 'ElegantIcons';
                        font-size: 14px;
                        padding-right: 5px;
                        color:#cacaca;
                        position: relative;
                        top: 2px;
                    }
                }
            
                > div {
                    position: relative;
                    display: inline-block;
                    padding-right: 15px;
                    margin-right: 3px;
                    color: $default-text-color;
                
                    a {
                        color: inherit;
                    
                        &:hover {
                            color: $default-heading-color;
                        }
                    }
                
                    &:last-child {
                        padding-right: 0;
                        margin-right: 0;
                    
                        &:after {
                            content: '';
                        }
                    }
                }
            }
        
            .eltdf-post-info-bottom {
                @include eltdfRelativeHolderLayout();
                margin: 31px 0 0;
            
                .eltdf-post-info-bottom-left,
                .eltdf-post-info-bottom-right {
                    position: relative;
                    display: inline-block;
                    width: 50%;
                    float: left;
                }
            
                .eltdf-post-info-bottom-left {
                
                    > div {
                        position: relative;
                        display: inline-block;
                        padding-right: 8px;
                        margin-right: 3px;
                        color: $default-text-color;
                    
                        a {
                            color: inherit;
                        
                            &:hover {
                                color: $default-heading-color;
                            }

                            span {
                                padding-left: 3px;
                            }
                        }
                    
                        &:after {
                            content: "/";
                            position: absolute;
                            top: 0;
                            right: -2px;
                        }
                    
                        &:last-child {
                            padding-right: 0;
                            margin-right: 0;
                        
                            &:after {
                                content: '';
                            }
                        }
                    }
                }
            
                .eltdf-post-info-bottom-right {
                    text-align: right;
                
                    > div {
                        margin-left: 17px;
                    
                        &:first-child {
                            margin-left: 0;
                        }
                    }
                }
            }

            &.format-gallery {
                .owl-nav {
                    .eltdf-prev-icon,
                    .eltdf-next-icon {
                        color: #000;

                        &:hover {
                            opacity:0.8;
                        }
                    }
                }
            }
        
            &.format-link {

                margin-bottom: 110px;

                .eltdf-post-additional-content {
                    margin-bottom: 50px;
                }

                .eltdf-post-content {
                    margin-bottom: 25px;
                }

                .eltdf-post-info-top {
                    margin-bottom: 21px;
                }

                .eltdf-post-text-main {
                    margin-bottom: 0;
                }

                .eltdf-post-text-inner {
                    margin-bottom: 0;
                }
            
                .eltdf-post-text {
                    padding: 30px 53px 37px;
                    background-color: #fbfbfb;
                }
            
                .eltdf-post-text-main {
                    @include eltdfTableLayout();
                }

                .eltdf-post-info-bottom {
                    text-align: center;
                    margin-top: 0;
                    margin-bottom: 0;
                }
            
                .eltdf-post-mark {
                    position: absolute;
                    top: -20px;
                    right: -68px;
                    width: 50px;
                
                    .eltdf-link-mark {
                        font-size: 24px;
                        color: #c7c7c7;
                    }
                }
            
                .eltdf-post-link-holder {
                    @include eltdfTableCellLayout();
                    text-align: center;
                }
            
                .eltdf-post-title {
                    margin-top: 15px;
                    margin-bottom: 5px;
                }
            }

            &.format-quote {

                margin-bottom: 110px;

                .eltdf-post-additional-content {
                    margin-bottom: 50px;
                }

                .eltdf-post-content {
                    margin-bottom: 40px;
                }

                .eltdf-post-text-main {
                    margin-bottom: 0;
                }

                .eltdf-post-text-inner {
                    margin-bottom: 0;
                }

                .eltdf-post-text {
                    padding: 35px 55px 45px;
                    background-color: #fafafa;
                }

                .eltdf-post-text-main {
                    @include eltdfTableLayout();
                    text-align: center;
                }

                .eltdf-post-info-bottom {
                    text-align: center;
                    margin-top: 0;
                    margin-bottom: 0;
                }

                .eltdf-post-mark {
                    @include eltdfTableCellLayout();
                    width: 50px;
                    vertical-align: top;
                    text-align: left;

                    .eltdf-quote-mark {
                        padding-top: 5px;
                        font-size: 36px;
                        color: $first-main-color;
                    }
                }

                .eltdf-post-link-holder {
                    @include eltdfTableCellLayout();
                    vertical-align: top;
                    text-align: left;
                }

                .eltdf-post-title {
                    margin-top: 15px;
                    margin-bottom: 5px;
                }

                .eltdf-quote-author {
                    font-size: 11px;
                    color: $default-heading-color;
                    letter-spacing: 2px;
                    font-weight: 500;
                }

                .eltdf-post-author {
                    display: inline-block;
                    margin-bottom: 23px;
                    font-size: 13px;
                    line-height: 20px;
                }
            }

            &.format-gallery {
                .owl-nav {
                    .eltdf-prev-icon,
                    .eltdf-next-icon {
                        font-size: 30px;
                        color: #000;

                        &:hover {
                            opacity:0.8;
                        }
                    }
                }
            }
        }
    }
}
/* ==========================================================================
   Blog Single Standard style - end
   ========================================================================== */