
.eltdf-header-vertical {

    .eltdf-vertical-alignment-center{
        .eltdf-vertical-menu-outer {
            display: table;
            padding: 100px 0 150px;
            width: 100%;
            position: relative;
            height: 100%;
            top: 0;
            left: 0;
            box-sizing: border-box;
        }

        .eltdf-vertical-menu-inner {
            display: table-cell;
            height: auto;
            min-height: 0;
            vertical-align: middle;
        }

        .eltdf-vertical-menu {
            display: table-cell;
            height: auto;
            min-height: 0;
            vertical-align: middle;
        }
    }

    .eltdf-vertical-menu {
        @include eltdfRelativeHolderLayout();
        margin: 45px 0 9px;
        padding: 0;
        z-index: 100;

        .second {
            .inner {
                ul {
                    padding: 19px 0 22px;
                    li {
                        a {
                            text-transform: none;
                            padding: 10px 35px;
                            line-height: 1.3em;
                        }

                        ul {
                            top: -19px;
                        }
                    }
                }
            }
        }
        
        ul {
            position: relative;
            list-style-type: none;
            padding: 0;
            margin: 0;
            
            li {
                position: relative;
                padding: 0;
                margin: 0;
                
                a {
                    line-height: 1.2em;
	                position: relative;
	                display: block;
	                margin: 0;
	                padding: 13px 0;
                    color: $first-main-color;
                    text-transform: uppercase;
                    
                    &:hover {
                        color: $default-text-color;
                    }
                    
                    .item_outer {
                        position: relative;
                        display: block;
                        padding: 0 15px 0 0;
                        box-sizing: border-box;
                    }
                    
                    .eltdf-menu-arrow {
                        position: absolute;
                        top: 0;
                        right: 0;
                        font-size: 12px;
                        line-height: inherit;
                        margin: 0;
                    }
                    
                    .eltdf-menu-featured-icon {
                        position: relative;
                        top: -4px;
                        left: 3px;
                        color: $default-heading-color;
                        font-size: 12px;
                        margin: 0;
                    }
                    
                    .menu_icon:not(.blank):not(.null) {
                        padding: 0 5px 0 0;
                    }
                }

	            &.eltdf-active-item > a,
	            &.current_page_item > a,
	            &.current-menu-item > a,
	            &.current-menu-ancestor > a {
		            color: $default-text-color;
                }
                
                ul {
                    padding: 3px 5px;
                    
                    li {
                        
                        &.sub {
                            
                            > a {
                                
                                .item_outer {
                                    position: relative;
                                    display: block;
                                    
                                }
                            }
                            
                        }
                    }
                }
                

            }
        }
        
        > ul {
            
            > li {
                
                > a {
                    font-weight: 600;
                    font-size: 13px;
                    letter-spacing: 1.5px;
                    
                    span.item_text {
	                    position: relative;
	                    display: inline-block;
	                    vertical-align: top;
                    }
                }

                &.eltdf-active-item > a,
                &.current_page_item > a,
                &.current-menu-item > a,
                &.current-menu-ancestor > a ,
                &:hover > a {
                    color: #a7a9af;
                }
            }
        }
    }
}

.eltdf-light-header.eltdf-header-vertical .eltdf-vertical-menu ul li {
    
    a {
        color: $header-light-color !important;
        
        &:hover {
            color: $header-light-hover-color !important;
        }
    }
    
    ul li {
        
        &.current_page_item > a,
        &.current-menu-item > a,
        &.current-menu-ancestor > a {
            color: $header-light-hover-color !important;
        }
    }
}

.eltdf-light-header.eltdf-header-vertical .eltdf-vertical-menu > ul > li {
    
    &.eltdf-active-item > a,
    &.current-menu-ancestor > a {
        color: $header-light-hover-color !important;
    }
}

.eltdf-dark-header.eltdf-header-vertical .eltdf-vertical-menu ul li {
    
    a {
        color: $header-dark-color !important;
        
        &:hover {
            color: $header-dark-hover-color !important;
        }
    }
    
    ul li {
        
        &.current_page_item > a,
        &.current-menu-item > a,
        &.current-menu-ancestor > a {
            color: $header-dark-hover-color !important;
        }
    }
}

.eltdf-dark-header.eltdf-header-vertical .eltdf-vertical-menu > ul > li {
    
    &.eltdf-active-item > a,
    &.current-menu-ancestor > a {
        color: $header-dark-hover-color !important;
    }
}
