/* ==========================================================================
   Blog List shortcode Common style - begin
   ========================================================================== */

.eltdf-blog-list-holder {
    @include eltdfRelativeHolderLayout();
    clear: both;

    .eltdf-blog-list {
        @include eltdfRelativeHolderLayout();
        list-style: none;
        margin: 0;
        padding: 0;
    }
	
	.eltdf-bl-item {
		@include eltdfRelativeHolderLayout();
		padding: 0;
		margin: 0;
		box-sizing: border-box;
	}
	
	.eltdf-bli-inner {
		@include eltdfRelativeHolderLayout();
	}
	
	.eltdf-bli-content {
		@include eltdfRelativeHolderLayout();
		box-sizing: border-box;
	}
	
	.eltdf-post-image {
		@include eltdfRelativeHolderLayout();
		
		a, img {
			display: block;
		}

		a {

			&:after {
				content:"";
				@include eltdfAbsoluteHolderLayout();
			    background:#fff;
			    opacity:0;
			    @include eltdfTransition(opacity .3s ease);
			}

			&:hover {
				&:after {
				    opacity:0.4;
				}
			}
		}

	}

	.eltdf-post-title {
		@include eltdfTransition(opacity .3s ease);

		&:hover {
			opacity: 0.7;
		}
	}
	
	.eltdf-bli-excerpt {
		@include eltdfRelativeHolderLayout();
		margin: 0;
	}

    .eltdf-blog-list-button {
        @include eltdfRelativeHolderLayout();
        margin: 26px 0 0;
    }


	
	/***** Columns Space style - begin *****/
	
	$columns_space_label: ('no', 'tiny', 'small', 'normal', 'large');
	$columns_space: (0, 5, 10, 15, 40);
	
	@for $i from 0 to length($columns_space_label) {
		&.eltdf-bl-#{nth($columns_space_label,$i+1)}-space {
			$column_width: nth($columns_space, $i+1);
			$column_width_margin: $column_width * 2;
			
			.eltdf-bl-wrapper {
				@if($column_width == 0) {
					margin: 0;
				} @else {
					margin: 0 -#{$column_width}px;
				}
			}
			
			.eltdf-bl-item {
				padding: 0 #{$column_width}px;
				margin: 0 0 #{$column_width_margin}px;
			}
		}
	}
	
	/***** Columns Space style - end *****/

	&.eltdf-bl-large-space {
		.eltdf-bl-item {
			margin: 0 0 30px;
		}
	}
}
/* ==========================================================================
   Blog List shortcode Common style - end
   ========================================================================== */