/* ==========================================================================
   Blog pagination standard style - begin
   ========================================================================== */

.eltdf-blog-pagination-wp {
    display: none;
}

.eltdf-blog-pagination {
    @include eltdfRelativeHolderLayout();
	margin: 5px 0px 67px;
    clear: both;
	padding-top: 41px;
	padding-bottom: 8px;
	border-top: 1px solid $default-border-color;
    
    ul {
        @include eltdfRelativeHolderLayout();
        padding: 0;
        margin: 0;
        list-style: none;
        text-align: center;
        
        li {
			position: relative;
			display: inline-block;
			vertical-align: top;
			padding: 0;
            
            a {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				padding: 0 2px;
				color: $first-main-color;
				font-size: 13px;
				font-weight: 600;
				line-height: 40px;
				width: auto;
				margin: 0 12px;
				text-decoration: none;
				cursor: pointer;
				white-space: nowrap;
            }

			a.eltdf-pag-active {
				&:after {
					content: '';
					background-color: $first-main-color;
					width: 100%;
					height: 1px;
					position: absolute;
					top:100%;
					left: 1px;
					display: inline-block;
				}
			}
            
            &.eltdf-pag-prev,
            &.eltdf-pag-next,
            &.eltdf-pag-first,
            &.eltdf-pag-last {
	            
	            a {
					font-size: 18px;
					color: $first-main-color;
					font-weight: 700;
					margin: 0 6px 0 4px;
		
		            span {
			            display: block;
			            line-height: inherit;
			
			            &:before {
				            display: block;
				            line-height: inherit;
			            }
		            }
	            }
            }


			&.eltdf-pag-first,
			&.eltdf-pag-last {
				a {
					span {
						font-weight: 600;
					}
				}
			}

        }
    }
}
/* ==========================================================================
   Blog pagination standard style - end
   ========================================================================== */

/* ==========================================================================
   Blog List shortcode pagination standard style - begin
   ========================================================================== */

.eltdf-blog-list-holder {
	
	&.eltdf-bl-pag-standard-blog-list {
		
		.eltdf-blog-list {
			opacity: 1;
			@include eltdfTransition(opacity .2s ease-out);
		}
		
		&.eltdf-bl-pag-standard-blog-list-animate {
			
			.eltdf-blog-list {
				opacity: 0;
			}
		}
	}
}

.eltdf-bl-standard-pagination {
	@include eltdfRelativeHolderLayout();
	margin: 20px 0 0;
	clear: both;
	
	ul {
		@include eltdfRelativeHolderLayout();
		padding: 0;
		margin: 0;
		list-style: none;
		text-align: center;
		
		li {
			position: relative;
			display: inline-block;
			vertical-align: top;
			margin: 0 12px;
			padding: 0;
			
			a {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				margin: 0;
				padding: 0;
			}
			
			&.eltdf-bl-pag-active {
				
				a {
					color: $first-main-color;
				}
			}
			
			&.eltdf-bl-pag-prev,
			&.eltdf-bl-pag-next {
				position: absolute;
				top: 0;
				
				a {
					font-size: 30px;
					@include eltdfTransition(color .2s ease-out, opacity .2s ease-out);
					
					span {
						display: block;
						line-height: inherit;
						
						&:before {
							display: block;
							line-height: inherit;
						}
					}
				}
			}
			
			&.eltdf-bl-pag-prev {
				left: 0;
				
				a {
					opacity: 0;
				}
			}
			
			&.eltdf-bl-pag-next {
				right: 0;
				
				a {
					opacity: 1;
				}
			}
		}
	}
}

/* ==========================================================================
   Blog List shortcode pagination standard style - end
   ========================================================================== */

/* ==========================================================================
   Blog pagination load more style - begin
   ========================================================================== */

.eltdf-blog-pag-load-more {
	@include eltdfRelativeHolderLayout();
	margin: 40px 0 0;
	text-align: center;
}
/* ==========================================================================
   Blog pagination load more style - end
   ========================================================================== */

/* ==========================================================================
   Blog pagination loading element style - begin
   ========================================================================== */

.eltdf-blog-pag-loading {
	position: relative;
	display: none;
	width: 100%;
	margin: 40px 0 20px;
	color: $default-heading-color;
	text-align: center;
	
	&.eltdf-standard-pag-trigger {
		position: absolute;
		top: 50px;
		left: 0;
	}
	
	&.eltdf-showing {
		display: block;
	}
	
	> div {
		position: relative;
		display: inline-block;
		vertical-align: middle;
		width: 14px;
		height: 14px;
		margin: 0 3px;
		background-color: $default-heading-color;
		border-radius: 100%;
		@include eltdfAnimation(bl-pag-bouncedelay 1.4s infinite ease-in-out both);
	}
	
	.eltdf-blog-pag-bounce1 {
		-webkit-animation-delay: -0.32s;
		-moz-animation-delay: -0.32s;
		animation-delay: -0.32s;
	}
	
	.eltdf-blog-pag-bounce2 {
		-webkit-animation-delay: -0.16s;
		-moz-animation-delay: -0.16s;
		animation-delay: -0.16s;
	}
}

@-webkit-keyframes bl-pag-bouncedelay {
	0%, 80%, 100% {
		-webkit-transform: scale(0);
	}
	40% {
		-webkit-transform: scale(1.0);
	}
}

@-moz-keyframes bl-pag-bouncedelay {
	0%, 80%, 100% {
		-moz-transform: scale(0);
	}
	40% {
		-moz-transform: scale(1.0);
	}
}

@keyframes bl-pag-bouncedelay {
	0%, 80%, 100% {
		-webkit-transform: scale(0);
		transform: scale(0);
	}
	40% {
		-webkit-transform: scale(1.0);
		transform: scale(1.0);
	}
}
/* ==========================================================================
   Blog pagination loading element style - end
   ========================================================================== */