/* ==========================================================================
   Title style - begin
   ========================================================================== */

.eltdf-title {
    @include eltdfRelativeHolderLayout();
    height: 325px;
    text-align: center;
    z-index: 101;
    background-position: center 0;
    background-repeat: no-repeat;

    &.eltdf-has-parallax-background {
        position: relative;
        background-attachment: fixed;
        background-repeat: no-repeat;
        z-index: 101;
    }

    &.eltdf-content-left-alignment {
        text-align: left;
    }

    &.eltdf-content-center-alignment {
        text-align: center;
    }

    &.eltdf-content-right-alignment {
        text-align: right;
    }

    &.eltdf-breadcrumb-type {

        .eltdf-title-holder {
            border-top: 1px solid $default-border-color;
            border-bottom: 1px solid $default-border-color;
        }
    }

    &.eltdf-has-responsive-background {
        height: auto !important;

        .eltdf-title-holder {
            position: absolute;
            top: 0;
            left: 0;
            display: block;

            .eltdf-container {
                display: block;
                height: 100%;
            }

            .eltdf-container-inner {
                display: table;
                height: 100%;
            }

            .eltdf-title-subtitle-holder {
                display: table-cell;
                vertical-align: middle;
                width: 100%;
            }
        }
    }

    .eltdf-title-image {

        img {
            position: relative;
            display: block;
            width: 100%;
        }
    }

    &.eltdf-title-image-responsive {
        height: auto !important;
    }

    &.eltdf-title-image-not-responsive {

        .eltdf-title-image {
            display: none;
        }
    }

    .eltdf-title-holder {
        position: relative;
        display: table;
        height: 100%;
        width: 100%;

        .eltdf-container {
            display: table-row;
            vertical-align: middle;
            background-color: transparent;
        }

        .eltdf-container-inner {
            display: table-cell;
            vertical-align: middle;
            padding: 0 !important;
        }

        .eltdf-title-subtitle-holder {
            position: relative;
            display: block;
            padding: 0;
            z-index: 25;
        }

        .eltdf-title-subtitle-holder-inner {
            position: relative; //because of the breadcrumbs (absolute position) on standard type title
        }

        /* #Standard title - start
        ========================================================================== */
    
        .eltdf-page-title {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            margin: 0;
    
            span {
                display: inline-block;
                vertical-align: middle;
            }
        }

        .eltdf-subtitle {
            display: block;
            margin-top: 4px;

            span {
                display: inline-block;
                vertical-align: middle;
            }
        }

        /* #Standard title - end
        ========================================================================== */

        /* #Breadcrumb title - start
        ========================================================================== */

        .eltdf-breadcrumbs-holder {
            display: inline-block;
            vertical-align: middle;
        }

        .eltdf-breadcrumbs {

            a, 
            span {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                color: $default-text-color;
            }

            a:hover {
                color: $default-heading-color;
            }
        }

        /* #Breadcrumb title - end
        ========================================================================== */
    }

    &.eltdf-standard-type {

        .eltdf-breadcrumbs-holder {
            position: absolute;
            top: 0;
            height: 100%;

            .eltdf-breadcrumbs {
                display: table;
                height: 100%;
                width: 100%;
            }

            .eltdf-breadcrumbs-inner {
                display: table-cell;
                vertical-align: middle;
            }

        }

        &.eltdf-content-left-alignment {

            .eltdf-breadcrumbs-holder {
                right: 0;
            }
        }

        &.eltdf-content-right-alignment {

            .eltdf-breadcrumbs-holder {
                left: 0;
            }
        }

        &.eltdf-content-center-alignment {

            .eltdf-breadcrumbs-holder {
                position: relative;
                top: auto;
                height: auto;
                display: block;
            }
        }

        h5.eltdf-page-title {
            font-size: 15px !important;
        }
    }

    &.eltdf-title-full-width {
        .eltdf-title-holder {
            .eltdf-container-inner {
                width: 100% ;
                padding: 0 30px !important;
                box-sizing: border-box;
            }
        }
    }
}


/* ==========================================================================
   Title style - end
   ========================================================================== */