.eltdf-header-box{
    .eltdf-top-bar {
        height: 55px;
    }

    .eltdf-page-header {

        .eltdf-menu-area {
            height: 90px;
            box-sizing: border-box;
            background-color: transparent;

            .eltdf-logo-wrapper a {
                max-height: 80px; //100*0.9
            }

            .eltdf-grid .eltdf-vertical-align-containers {
                box-sizing: initial;
                margin: 0 -35px;
                padding: 0 35px !important;
                background-color: #fff;
            }

            .eltdf-position-right-inner {
                height: 100%;
            }
        }
    }

    .eltdf-drop-down .wide .second > .inner > ul {
        width: 1170px; //1100 + 70, 70 is 35 of padding on each side
    }

    &.eltdf-grid-1000 .eltdf-drop-down .wide .second > .inner > ul {
        width: $grid-width-1000+70;
    }

    &.eltdf-grid-800 .eltdf-drop-down .wide .second > .inner > ul {
        width: $grid-width-800+70;
    }

}
@media only screen and (min-width: 1400px) {
	.eltdf-header-box.eltdf-grid-1300 .eltdf-drop-down .wide .second > .inner > ul {
		width: $grid-width-1300+70;
	}
}
@media only screen and (min-width: 1300px) {
	.eltdf-header-box.eltdf-grid-1200 .eltdf-drop-down .wide .second > .inner > ul {
		width: $grid-width-1200+70;
	}
}

