.eltdf-header-centered {
    .eltdf-page-header {
        .eltdf-logo-area {
            height: 134px;
            z-index: 2; //to be above menu area in order to cover shadow
            overflow: visible;

            .eltdf-logo-wrapper a {
                max-height: 146px; //162*0.9
            }

            box-sizing: border-box;
        }

        .eltdf-menu-area {
            z-index: 1;
            height: 76px;

            box-sizing: border-box;

            .eltdf-vertical-align-containers {
                .eltdf-position-center-inner {
                    height: 100%;
                }
            }
        }

        .eltdf-page-header .eltdf-sticky-header {
            .eltdf-position-left-inner {
                height: auto;
            }

            .eltdf-position-right-inner {
                height: 100%;
            }
        }
    }
}

