/* ==========================================================================
   Blog Single Title Area Empty style - begin
   ========================================================================= */

.eltdf-blog-single-title-area-empty {
	article {

		.eltdf-post-title:hover {
           opacity: 1;
		}

		&.format-video {
			.eltdf-post-heading {
				padding-bottom: 40px;
			}
		}

		&.format-gallery {
			.owl-nav {
				.eltdf-prev-icon,
				.eltdf-next-icon {
					font-size: 30px;
					color: #000;
				}
			}

			.eltdf-post-heading {
				padding-bottom: 40px;
			}
		}

		&.format-audio {
			.eltdf-post-heading {
				padding-bottom: 40px;
			}
		}

		&.format-link {

			margin-bottom: 110px;

			.eltdf-post-additional-content {
				margin-bottom: 50px;
			}

			.eltdf-post-content {
				margin-bottom: 40px;
			}

			.eltdf-post-text-main {
				margin-bottom: 0;
			}

			.eltdf-post-text-inner {
				margin-bottom: 0;
			}

			.eltdf-post-text {
				padding: 30px 53px 37px;
				background-color: #fbfbfb;
			}

			.eltdf-post-text-main {
				@include eltdfTableLayout();
			}

			.eltdf-post-info-bottom {
				text-align: center;
				margin-top: 0;
				margin-bottom: 0;
			}

			.eltdf-post-mark {
				@include eltdfTableCellLayout();
				width: 50px;

				.eltdf-link-mark {
					font-size: 36px;
					color: $first-main-color;
				}
			}

			.eltdf-post-link-holder {
				@include eltdfTableCellLayout();
			}

			.eltdf-post-title {
				margin-bottom: 0;
				margin-top: 0;
			}
		}

		&.format-quote {

			margin-bottom: 110px;

			.eltdf-post-additional-content {
				margin-bottom: 50px;
			}

			.eltdf-post-content {
				margin-bottom: 40px;
			}

			.eltdf-post-text-main {
				margin-bottom: 0;
			}

			.eltdf-post-text-inner {
				margin-bottom: 0;
			}

			.eltdf-post-text {
				padding: 30px 53px 45px;
				background-color: #fafafa;
			}

			.eltdf-post-text-main {
				@include eltdfTableLayout();
				text-align: center;
			}

			.eltdf-post-info-bottom {
				text-align: center;
				margin-top: 0;
				margin-bottom: 0;
			}

			.eltdf-post-mark {
				@include eltdfTableCellLayout();
				width: 50px;
				vertical-align: top;
				text-align: left;

				.eltdf-quote-mark {
					padding-top: 5px;
					font-size: 36px;
					color: $first-main-color;
				}
			}

			.eltdf-post-link-holder {
				@include eltdfTableCellLayout();
				vertical-align: top;
				text-align: left;
			}

			.eltdf-post-title {
				margin-bottom: 0;
			}

			.eltdf-quote-author {
				font-size: 11px;
                color: $default-heading-color;
                letter-spacing: 2px;
                font-weight: 500;
			}

			.eltdf-post-author {
				display: inline-block;
				margin-bottom: 23px;
				font-size: 13px;
				line-height: 20px;
			}
		}

		.eltdf-post-text-inner {
			margin-bottom: 110px;
		}

		.eltdf-post-text-main {
			margin-bottom: 55px;
		}
		.eltdf-post-content {
			.eltdf-post-info-bottom {
				text-align: center;
				margin-top: 0;
				margin-bottom: 0;
			}
		}

		.eltdf-tags {
			a {
				display: inline-block;
				text-transform: uppercase;
				color: #848484;
				font-size: 9px;
				font-weight: 600;
				padding: 0 25px;
				background-color: transparent;
				border: 1px solid #d3d3d3;
				height: 29px;
				line-height: 31px;
				margin-right: 3px;
				margin-bottom: 10px;
			}
		}

	}
}

/* ==========================================================================
   Blog Single Title Area Empty style - end
   ========================================================================= */