.eltdf-blog-list-holder {
    &.eltdf-bl-simple {

        .eltdf-post-title {
            margin: -10px 0 0;
        }

        .eltdf-post-image {
            display: table-cell;
            vertical-align: top;
            width: auto;
        }

        .eltdf-bli-content {
            display: table-cell;
            vertical-align: top;
            width: auto;
            padding-left: 20px;
        }

        .eltdf-post-info-date {

            a {
                font-size: 11px;
                letter-spacing: .2em;
                line-height: 40px;
                font-weight: 500;
                color: $default-text-color;
            }
        }

        .eltdf-post-info-comments-holder {
            display: inline-block;
            margin-right: 11px;

            a {
                font-size: 11px;
                color: $default-heading-color;
                font-weight: 500;
                &:before {
                    content: "\76";
                    font-family: 'ElegantIcons';
                    font-size: 14px;
                    padding-right: 5px;
                    color:#cacaca;
                    position: relative;
                    top: 2px;
                }
            }
        }

        .eltdf-post-info-category {
            display: inline-block;
            a {
                text-transform: uppercase;
                font-size: 11px;
                letter-spacing: 2px;
                color: $default-heading-color;
                font-weight: 500;
            }

             &:before {
                content: "\e012";
                font-family: 'ElegantIcons';
                font-size: 14px;
                padding-right: 5px;
                color:#cacaca;
                position: relative;
                top: 2px;
            }
        }

    }
}