@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.eltdf-comment-holder {
    @include eltdfRelativeHolderLayout();
    margin: 0px -1000px;
    padding: 0px 1000px;
    border-bottom: 1px solid #e6e6e6;
    padding-bottom: 52px;

    &.eltdf-comment-holder-no-comments {
        padding-bottom: 0;
        .eltdf-comment-holder-inner {
            padding: 0;
        }
    }

    .eltdf-comment-holder-inner {
        padding: 38px 0 18px 31px;
    }
	
	.eltdf-comment {
		
		&.eltdf-pingback-comment {
			
			.eltdf-comment-text {
				padding: 0;
			}
		}
	}

    .eltdf-comment-list {
        list-style: none;
        position: relative;
        z-index: 150;
        padding: 0;
        margin: 0;
        padding-top: 17px;

        li {
            @include eltdfRelativeHolderLayout();
            margin: 30px 0 0;
            padding: 29px 0 0;
        }

        > li:first-child {
            margin: 0;
            padding: 0;
            border: 0;
        }

        .children {
            padding: 0 0 0 112px;
        }
    }

    .eltdf-comments-title {
        margin-bottom: 28px;
        margin-top: 28px;

        h3 {
            letter-spacing: 0.1em;
            text-transform: uppercase;
        }

        > * {
            margin: 0;
        }
    }

    .eltdf-comment-image {
        position: relative;
        display: block;
        top: 10px;
        left: 0;
        width: 82px;
        height: 82px;
        float: left;

        img {
            display: block;
            border-radius: 4em;
        }
    }

    .eltdf-comment-text {
        padding: 0 0 0 112px;
        min-height: 82px;

        .eltdf-comment-info {
            @include eltdfRelativeHolderLayout();
            margin: 6px 0 1px;
        }

        .replay,
        .comment-reply-link,
        .comment-edit-link {
            float: left;
            font-size: 11px;
            color: $default-heading-color;
            text-transform: uppercase;
            font-weight: 500;
            letter-spacing: 2px;

            &:hover {
                color: $default-heading-color;
            }
        }

        .comment-reply-link {
            margin-top: -5px;
            &:after {
                font-family: 'Linearicons-Free';
                content: "\e87a";
                font-size: 13px;
                padding-left: 10px;
                color: $default-heading-color;
                font-weight: 700;
                position: relative;
                vertical-align: top;
            }
        }

        .eltdf-comment-name {
            float: left;
            margin: 0;
            text-transform: uppercase;

            span {
                color: $default-text-color;
                font-size: 11px;
                letter-spacing: 2px;
                padding-left: 12px;
                font-family: $default-text-font;
            }
        }

        .eltdf-comment-date {
            float: left;
            margin: 0;
            font-size: 12px;
        }

        .eltdf-text-holder p {
            margin: 0 0 15px;
            color: $default-text-color;
        }

        .comment-respond {
            margin: 20px 0;

            .comment-reply-title {
                margin: 0 0 10px;
                font-size: 14px;
            }

            .logged-in-as {
                margin: 0 0 8px;
            }
        }

        #cancel-comment-reply-link {
            margin-left: 10px;
            font-size: 11px;
            color: $first-main-color;

            &:hover {
                color: $default-heading-color;
            }
        }

        li {
            margin: 0 !important;
            padding: 0 !important;
            border: 0 !important;
        }
    }
}

.eltdf-comment-form {
    padding: 0;
    clear: both;
    box-sizing: border-box;
    
    .eltdf-comment-form-inner {
        padding: 66px 0 76px;
    }
    
    .comment-reply-title {
        margin: 0 0 32px;
    }
    
    .logged-in-as {
        margin: 0 0 8px;
    }
}

@include laptop-landscape-medium {

    .eltdf-comment-holder {

        .eltdf-comment-list {

            .children {
                padding: 0 0 0 50px;
            }
        }
    }
}

@include ipad-landscape {

    .eltdf-comment-holder {

        .eltdf-comment-list {

            .children {
                padding: 0 0 0 30px;
            }
        }
    }
}

@include ipad-portrait {

    .eltdf-comment-holder {
	    
        .eltdf-comment-list {
	        
            .children {
                padding: 0;
            }
        }
    }
}

@include phone-landscape {
	
    .eltdf-comment-holder {
	    
        .eltdf-comment-list {
	        
            .children {
                padding: 0;
            }
        }

        .eltdf-comment-image {
            float: none;
        }

        .eltdf-comment-text {
            padding: 40px 0 0;
	        
            .eltdf-comment-date,
            .eltdf-comment-name {
                float: none;
            }
	        
            .replay,
            .comment-reply-link,
            .comment-edit-link {
                float: none;
                margin: 0 10px 0 0;
            }
        }
    }
}

@include phone-portrait {
	
    .eltdf-comment-holder {
	    
        .eltdf-comment-list {
	        
            .children {
                padding: 0;
            }
        }

        .eltdf-comment-image {
            float: none;
        }

        .eltdf-comment-text {
            padding: 20px 0 0;
            min-height: inherit;
        }
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

%input-style {
    width: 100%;
    margin: 0 0 15px;
    padding: 16px 24px 15px;
    font-family: inherit;
    font-size: 14px;
    line-height: 20px;
    font-weight: inherit;
    color: $default-heading-color;
    background-color: transparent;
    border: 1px solid #d3d3d3;
    border-radius: 0;
    outline: 0;
    -webkit-appearance: none;
    box-sizing: border-box;
    @include eltdfTransition(border-color 0.2s ease-in-out);

    &:focus {
        color: $default-heading-color;
        border-color: #d3d3d3;
        background-color: #fafafa;
    }
    
    @include eltdfPlaceholder {
        color: $default-text-color;
    }
}

#respond textarea,
#respond input[type='text'],
.post-password-form input[type='password'] {
    @extend %input-style;
    margin-top: 4px;
    padding: 21px 24px 21px;
    width: 78%;
    line-height: 21px;
    font-size: 15px;
}

.post-password-form input[type='password'] {
    width: 50%;
}

#respond textarea {
    display: block;
    padding: 21px 24px;
    height: 180px;
    width:  78%;
}

p.form-submit {
    margin: 0;
    margin-top: 7px;
}

#submit_comment,
.post-password-form input[type='submit'] {
    @include eltdfButtonDefaultStyle();
    @include eltdfButtonMediumParams();
    @include eltdfButtonSolidColor();
    padding: 13px 27px;
    cursor: pointer;
    margin-bottom: 4px;

    &:hover {
        @include eltdfButtonSolidHoverColor();
    }
}

.post-password-form input[type='submit'] {
    height: 65px;
}

.eltdf-newsletter {
    text-align: center;
    position: relative;
    .wpcf7-not-valid-tip {
        margin-top: 80px;
        margin-bottom: 20px;
    }
    .wpcf7-form-control {
        &.wpcf7-text {
            margin: 0 0 15px;
            padding: 20px 200px 18px 23px;
            float: left;
            height: 59px;
        }
    }
}

.eltdf-newsletter {
    .wpcf7-form-control {
        &.wpcf7-submit {
            padding: 15px 33px;
            font-size: 13px;
            letter-spacing: 2.6px;
            border: none;
            position: absolute;
            right: 0;
            top: 0;
            height: 59px;
            -webkit-border-radius: 0;
            -moz-border-radius: 0;
            border-radius: 0;
            cursor:pointer;
            @include eltdfTransition(color .2s ease-in-out, background-color .2s ease-in-out, border-color .2s ease-in-out);

            &:hover {
                @include eltdfButtonSolidHoverColor();
            }
        }
    }

    .eltdf-two-columns-form-without-space {
        .eltdf-column-left {
            display: inline-block;
            width: 100%;
        }
        .eltdf-column-right {
            display: inline-block;
            width: 0;
        }
    }
}

@include ipad-landscape {
    .eltdf-newsletter {
        .wpcf7-form-control {
            &.wpcf7-text {
                padding: 20px 150px 18px 23px;
            }
        }
    }
}

@include ipad-portrait {
    .eltdf-newsletter {
        .wpcf7-form-control {
            &.wpcf7-text {
                padding: 20px 100px 18px 23px;
            }
        }
    }
}

@include phone-portrait {
    .eltdf-newsletter {
        .wpcf7-form-control {
            &.wpcf7-text {
                padding: 20px 50px 18px 23px;
            }

            &.wpcf7-submit {
                position: relative;
            }
        }

        span.ajax-loader {
            display: block;
            margin: 10px auto 0;
        }
    }
}

%cf7-input-style {
    width: 100%;
    margin: 2px 17px 15px 0px;
    padding: 12px 18px;
    font-family: inherit;
    font-size: 15px;
    line-height: 21px;
    font-weight: inherit;
    color: #989899;
    background-color: transparent;
    border: 1px solid $default-grey-color;
    border-radius: 0;
    outline: 0;
    -webkit-appearance: none;
    box-sizing: border-box;

    &:focus {
        color: $default-heading-color;
        background-color: #fafafa;
    }
	
	@include eltdfPlaceholder {
		color: #989899;
	}
}

.wpcf7-form-control {
    &.wpcf7-text,
    &.wpcf7-number,
    &.wpcf7-date,
    &.wpcf7-textarea,
    &.wpcf7-select,
    &.wpcf7-quiz {
        @extend %cf7-input-style;
        margin-top: 2px;

        footer & {
            margin: 0;
        }
    }
}

input[type="search"]::-webkit-search-cancel-button {
    -webkit-appearance: none;

}

input.wpcf7-form-control.wpcf7-submit {
    @include eltdfButtonDefaultStyle();
    @include eltdfButtonMediumParams();
    @include eltdfButtonSolidColor();
    -webkit-border-radius: 0;
    -moz-border-radius: 0;
    border-radius: 0;
    cursor: pointer;
    letter-spacing: 2.6px;

    &:hover {
        @include eltdfButtonSolidHoverColor();
    }
}

.wpcf7-form p {
    margin: 0;
}

.wpcf7-form textarea {
    vertical-align: middle;
    height: 192px;
    margin-bottom: 20px !important;
}

.eltdf-two-columns-form-without-space {

    .wpcf7-form .ajax-loader {
        display: none;
    }
}

.eltdf-comment-holder .eltdf-comment-list li .eltdf-comment-text ul li,
.eltdf-comment-holder .eltdf-comment-list li .eltdf-comment-text ol li {
    display: list-item;
}
/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.eltdf-owl-slider{
    visibility: hidden;

    .owl-stage-outer {
        z-index: 1;
    }

    &.eltdf-slider-has-both-nav {

        .owl-nav {

            .owl-prev,
            .owl-next {
                $pos: calc(-50% - 19px); // 19 is is half height of pagination control
                @include eltdfTransform(translateY($pos));
            }
        }
    }

    .owl-nav {

        .owl-prev,
        .owl-next {
            position: absolute;
            top: 50%;
            z-index: 3;
            @include eltdfTransform(translateY(-50%));
        }

        .owl-prev {
            left: 30px;
        }

        .owl-next {
            right: 30px;
        }

        .eltdf-prev-icon,
        .eltdf-next-icon {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            color: $default-heading-color;
            line-height: 1;
            @include eltdfTransition(color .2s ease-out);

            .eltdf-icon-arrow {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                font-size: 30px;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }
    }

    .owl-dots {
        text-align: center;
        margin: 16px 0 0;

        .owl-dot {
            display: inline-block;
            vertical-align: middle;

            span {
                display: inline-block;
                vertical-align: middle;
                width: 12px;
                height: 12px;
                margin: 0 6px;
                border-radius: 3em;
                background-color: transparent;
                border: 2px solid rgba($default-heading-color, .5);
                @include eltdfTransition(background-color .2s ease-out, border .2s ease-out);
                box-sizing: border-box;
            }

            &:hover,
            &.active {

                span {
                    background-color: $default-heading-color;
                    border-color: $default-heading-color;
                }
            }
        }
    }

    .owl-carousel .owl-item img {
        -webkit-transform: translateZ(0); //chrome text cut off fix
    }
}
/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.eltdf-404-page {

    &.eltdf-wrapper {
        padding: 0 !important;
    }
    
    .eltdf-content {
    }

    .eltdf-content-inner {
        position: relative;
        display: table;
        height: 100%;
        width: 100%;
    }

    .eltdf-page-not-found {
        position: relative;
        display: table-cell;
        width: 100%;
        vertical-align: middle;
        padding: 0 20%;
        text-align: center;
        height: 100vh;

        @media screen and (max-width: 1000px) {
            height: calc(100vh - 100px);
        }



        .eltdf-icon-shortcode {
            font-size: 72px;
            color: $first-main-color;
            margin-bottom: 40px;
        }

        .eltdf-page-not-found-title {
            margin: 0;
            letter-spacing: 0.1em;
        }
	
	    
	    .eltdf-page-not-found-text {
            font-size: 20px;
            font-family: $alternative-text-font;
            font-style: italic;
            width: 62%;
            margin: 20px auto 0;
            line-height: 33px;
	    }

        .eltdf-btn {
            margin: 30px 0 0;
        }
    }

    @include ipad-portrait {

        .eltdf-page-not-found {
            padding: 0 30px;
        }
    }

    @include laptop-landscape-medium {

        .eltdf-404-title-image {
            margin-top: 48px;
        }
    }
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#eltdf-back-to-top {
    position: fixed;
    width: 41px;
    right: 24px;
    bottom: 20px;
    margin: 0;
    z-index: 10000;
    opacity: 0;
    visibility: hidden;

    &.off {
        opacity: 0;
        visibility: hidden;
        @include eltdfTransition(all 0.2s ease);
        @include eltdfTransform(translateY(100%));
    }

    &.on {
        opacity: 1;
        visibility: visible;
        @include eltdfTransition(all 0.3s ease);
        @include eltdfTransform(translateY(0));
    }

    &.eltdf-light {
        > span {
            color: #fff;
        }
    }

    > span {
        position: relative;
        display: block;
        width: 39px;
        height: 39px;
        line-height: 39px;
        font-size: 30px;
        text-align: center;
        color: #000;
        overflow: hidden;
        @include eltdfTransition(color 0.2s ease-in-out, background-color 0.2s ease-in-out, border-color 0.2s ease-in-out);

        i {
            @include eltdfTransition(all .2s ease .05s);
        }
    }

    .eltdf-icon-stack { 
        @include eltdfTransition(all .2s ease .05s);
    }

    &:hover {
        .eltdf-icon-stack {
            @include eltdfTransform(translateY(-7px));
        }
    }

    span span {
        display: block;
        line-height: inherit;

        &:before {
            content: "\e873";
            font-family: "Linearicons-Free";
        }
    }
}
/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

/* ==========================================================================
   #Self Hosted Video styles - begin
   ========================================================================== */
.eltdf-self-hosted-video-holder {
    .eltdf-video-wrap {
        .mejs-container,
        .mejs-overlay-play {
            height: 100% !important; /*!important because of the inline style*/
            width: 100% !important; /*!important because of the inline style*/
        }
    }
}
/* ==========================================================================
   #Self Hosted Video styles - end
   ========================================================================== */

   /* ==========================================================================
   #Newsletter responsive - begin
   ========================================================================== */

    @media only screen and (max-width: 480px) {
        .eltdf-cf-wrapper.eltdf-newsletter {
            .eltdf-two-columns-form-without-space {
                .eltdf-column-left {
                    display: block;
                    width: 100%;
                }

                .eltdf-column-right {
                    margin-top: 20px;
                    display: inline-block;
                    width: 100%;
                }
            } 
        }
    }

/* ==========================================================================
   #Newsletter responsive  - end
   ========================================================================== */